.hidden {
  opacity: 0;
}

.show {
  opacity: 1;
}

.section-link {
  transform: translateY(-80%);

  &:first-child {
    transition: transform 1s;
  }
  &:nth-child(2) {
    transition: transform 1s 0.5s;
  }
  &:nth-child(3) {
    transition: transform 1s 1s;
  }
  &.show {
    transform: translateY(0);
  }
}

.hero-title {
  transition: all 1s ease-in;
  filter: blur(5px);

  &.show {
    filter: blur(0);
  }
}

.hero-btn {
  transition: opacity 1s 1s, color 0.2s ease-in;
}

.about-img-container,
.about-desc {
  filter: blur(5px);
  transform: translateX(-100%);
  transition: all 1s;

  &.show {
    filter: blur(0);
    transform: translateX(0);
  }
}

.about-desc {
  transition-delay: 0.2s;
}

#text-sphere {
  transform: scale(0);
  transition: all 1s 1.5s;

  &.show {
    transform: scale(1);
  }
}

.about-skills-title {
  transform: scale(0);
  transition: all 1s 0.5s;

  &.show {
    transform: scale(1);
  }
}

.swiper-nav-btn {
  scale: 0;
  transition: scale 1s;

  &.show {
    scale: 1;
  }
}

.contact-text {
  filter: blur(3px);
  transition: filter 1s;

  &.show {
    filter: blur(0);
  }
}

#name-input,
#email-input,
#message-input {
  transition: opacity 1s;
}
#name-input {
  transition-delay: 0.3s;
}
#email-input {
  transition-delay: 0.8s;
}
#message-input {
  transition-delay: 1.3s;
}
#contact-btn {
  transition: opacity 1s 1.8s;
}
