* {
  margin: 0;
}

html, body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  position: relative;
  overflow-x: hidden;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

input, button, textarea {
  font-family: inherit;
}

* {
  scrollbar-width: thin;
  scrollbar-color: #313131 black;
}

html {
  font-size: 62.5%;
}

@media (max-width: 600px) {
  html {
    font-size: 50%;
  }
}

body {
  -webkit-font-smoothing: antialiased;
  color: #f5f5f5;
  font-family: Montserrat, sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

#typewriter {
  font-family: Source Code Pro, monospace;
}

.section-title {
  color: #f5f5f5;
  font-family: Major Mono Display, monospace;
  font-size: 3rem;
}

.hlred {
  color: #9d3333;
  font-weight: 600;
}

.hlblue {
  color: #51a2e9;
  font-weight: 600;
}

nav {
  width: 100%;
  height: 5rem;
  z-index: 10;
  align-items: center;
  gap: 1rem;
  transition: background-color .2s ease-in-out;
  display: flex;
  position: fixed;
}

nav.background {
  background-color: #00000090;
}

nav .section-links {
  gap: 2rem;
  margin-right: 5rem;
  display: flex;
}

@media (max-width: 600px) {
  nav .section-links {
    margin-right: 2rem;
  }
}

.section-link {
  color: #f5f5f5;
  padding: 2rem .3rem;
  position: relative;
}

.section-link:before {
  content: "";
  width: 100%;
  height: .1rem;
  transform-origin: 100% 100%;
  background-color: #f5f5f5;
  transition: transform .25s ease-out;
  position: absolute;
  top: 75%;
  left: 0;
  transform: scaleX(0);
}

.section-link:hover:before {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

#top-btn {
  cursor: pointer;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  margin-left: 5rem;
  margin-right: auto;
  transition: all .3s ease-in-out .1s;
  display: flex;
  top: -100%;
}

@media (max-width: 600px) {
  #top-btn {
    margin-left: 2rem;
  }
}

#top-btn.show {
  pointer-events: all;
  top: 0;
}

#hero {
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  display: flex;
  position: fixed;
}

.hero-title {
  color: #f5f5f5;
  text-align: center;
  font-size: 4rem;
  font-weight: 300;
  display: none;
}

.hero-btn {
  color: #f5f5f5;
  border: .1rem solid #f5f5f5;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  font-size: 2rem;
  transition: color .2s ease-in-out;
  display: none;
  position: relative;
  overflow: hidden;
}

.hero-btn:hover {
  color: #141517;
}

.hero-btn:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #ffffffa5;
  transition: all .2s ease-in-out;
  position: absolute;
  right: 100%;
}

.hero-btn:hover:before {
  right: 0%;
}

