  min-height: 100%;
  max-width: 100%;
  z-index: 1;
  background: #16161b;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: relative;
  top: 100%;
  overflow: hidden;
  box-shadow: 0 5px 15px #00000059;
}

@media (max-width: 1500px) {
  #about {
    gap: 5rem;
  }
}

.about-title {
  margin-top: 7rem;
  position: relative;
}

.about-title:before, .about-title:after {
  content: "";
  width: 200%;
  height: .1rem;
  background-color: #51a2e9;
  position: absolute;
  top: 50%;
  right: 110%;
}

.about-title:after {
  top: 50%;
  left: 110%;
}

.about-main {
  justify-content: center;
  align-items: center;
  gap: 7rem;
  padding: 0 5rem;
  display: flex;
}

