  height: fit-content;
  flex-direction: column;
  justify-content: center;
  gap: 6rem;
  display: flex;
}

#about img {
  width: 50rem;
  position: relative;
  box-shadow: 0 5px 15px #00000059;
}

.about-img-container {
  width: -moz-fit-content;
  width: fit-content;
  place-items: center;
  display: grid;
  position: relative;
}

.about-img-container:before {
  content: "";
  height: 100%;
  width: 100%;
  border: .1rem solid #51a2e9;
  position: absolute;
  top: 5%;
  left: 5%;
}

.typewriter-container {
  font-size: 4rem;
  display: flex;
  position: absolute;
}

#typewriter {
  width: max-content;
  white-space: nowrap;
  border-right: .1rem solid #f5f5f5;
  animation: blink .75s step-end infinite;
}

@keyframes blink {
  from, to {
    border-color: #0000;
  }

  50% {
    border-color: #f5f5f5;
  }
}

.about-desc {
  color: #c5c5c5;
  max-width: 50rem;
}

.about-skills-container {
  min-height: 60rem;
  height: min-content;
  place-items: center;
  display: grid;
  position: relative;
}

@media (max-width: 600px) {
  .about-skills-container {
    max-height: 50rem;
  }
}

.about-skills-title {
  color: #bfbfbf;
  place-items: center;
  font-size: 4rem;
  display: grid;
  position: absolute;
}

.about-skills-title:before {
  content: "";
  width: 20rem;
  height: 20rem;
  z-index: -1;
  border: .1rem solid #51a2e9;
  border-radius: 50%;
  position: absolute;
}

#text-sphere {
  cursor: grab;
}

@media (max-width: 600px) {
  #text-sphere {
    position: absolute;
    scale: .5;
  }
}

@media (max-width: 300px) {
  #text-sphere {
    position: absolute;
    scale: .3;
  }
}

.tagcloud {
  letter-spacing: .2rem;
  width: 20rem;
  font-family: Bebas Neue, cursive;
  font-size: 2.5rem;
  font-weight: bold;
}

@media (max-width: 600px) {
  .tagcloud {
    font-size: 4rem;
  }
}

.tagcloud--item {
  color: #51a2e9;
  transition: all .2s;
}

.tagcloud--item:hover {
  color: #ff4d5a;
  cursor: default;
}

#projects {
  height: 100%;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  display: flex;
  position: relative;
  top: 100%;
  overflow: hidden;
}

.projects-title {
  margin-top: 7rem;
  font-weight: 600;
  position: relative;
}

@media (max-width: 600px) {
  .projects-title {
    margin-top: 19rem;
    position: absolute;
  }
}

.projects-title:before, .projects-title:after {
  content: "";
  width: 200%;
  height: .1rem;
  background-color: #4b4b4b;
  position: absolute;
  top: 50%;
  right: 110%;
}

.projects-title:after {
  top: 50%;
  left: 110%;
}

.slide {
  flex-direction: column;
  display: flex;
}

@media (max-width: 1000px) {
  .slide {
    width: 100%;
    justify-content: center;
  }
}

.project-container {
