  .project-container {
    margin-top: 4rem;
  }
}

.project-info p {
  color: #c5c5c5;
}

.project-options {
  justify-content: center;
  gap: 3rem;
  display: flex;
}

#contact {
  height: 100%;
  z-index: 1;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: relative;
  top: 100%;
  overflow: hidden;
}

.contact-title {
  margin-top: 15rem;
  margin-bottom: 5rem;
  font-weight: 600;
  position: relative;
}

