  .contact-title {
    margin-top: 20rem;
  }
}

.contact-title:before, .contact-title:after {
  content: "";
  width: 170%;
  height: .1rem;
  background-color: #4b4b4b;
  position: absolute;
  top: 50%;
  right: 110%;
}

.contact-title:after {
  top: 50%;
  left: 110%;
}

.contact-text {
  text-align: center;
  width: 50rem;
  color: #c5c5c5;
  padding: 1rem;
}

#contact-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem 5rem;
  display: flex;
}

#contact-form input, #contact-form textarea {
  color: #f5f5f5;
  width: 40rem;
  background-color: #292929;
  border: none;
  border-left: .2rem solid #0000;
  outline: none;
  padding: 1rem;
  font-size: 1.7rem;
  position: relative;
}

#contact-form input.error, #contact-form textarea.error {
  border-left: .2rem solid red;
}

#contact-form input.error:focus, #contact-form textarea.error:focus {
  border-left: .2rem solid #0000;
}

#contact-form textarea {
  resize: none;
  height: 15rem;
}

.form-footer {
  width: 100%;
  display: flex;
}

#error-container {
  width: 100%;
  color: red;
}

#contact-btn {
  width: 14rem;
  height: 4rem;
  cursor: pointer;
  color: #000;
  background-color: #f5f5f5;
  border: .2rem solid #f5f5f5;
  border-radius: 0;
  justify-content: center;
  place-self: flex-start flex-end;
  align-items: center;
  margin-top: 1rem;
  font-size: 1.7rem;
  font-weight: 400;
  display: flex;
  position: relative;
  overflow: hidden;
}

#contact-btn p {
  z-index: 1;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  transition: all .3s;
  display: flex;
}

#contact-btn svg {
  transition: all .4s;
}

#contact-btn:before {
  content: "";
  width: 300%;
  height: 300%;
  background-color: #151515;
  transition: all .4s;
  position: absolute;
  bottom: 100%;
  right: 100%;
  rotate: 25deg;
}

#contact-btn:hover:before {
  bottom: 0;
  right: -15%;
}

#contact-btn:hover, #contact-btn:hover svg {
  color: #f5f5f5;
}

#hero-canvas {
  z-index: -2;
  height: 100%;
  width: 100%;
  background-color: #141517;
  position: absolute;
}

footer {
  height: 10rem;
  width: 100%;
  background-color: #00000050;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  display: flex;
  position: absolute;
  bottom: 0;
}

.footer-btns {
  gap: 3rem;
  display: flex;
}

.footer-svg {
  width: 2rem;
  height: 2rem;
  transition: all .1s;
  scale: 1.5;
}

.footer-svg:hover {
  scale: 1.7;
}

.red-btn {
  color: #f5f5f5;
  justify-content: center;
  padding: 1rem 2rem;
  font-weight: 600;
  display: flex;
  position: relative;
  overflow: hidden;
}

.red-btn:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #ff1d1d86;
  transition: all .15s ease-in-out;
  position: absolute;
  bottom: -98%;
}

.red-btn:hover:before {
  bottom: 0%;
}

